  border-radius: 20%;
} */

.sub-article__image {
  position: absolute;
  left: 0;
  top: 14%;
  width: 36%;
  height: 76%;
  border-radius: 20%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.sub-article__info {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  right: 0;
  top: 6%;
  width: 60%;
  height: 88%;
}

.sub-article__title {
  font-size: 4vw;
  color: #222;
  margin-bottom: 12px;
  font-weight: 600;
}

.sub-article__tags {
  width: 96%;
  font-size: 2.88vw;
  /* border: 1px solid black; */
}

.sub-article__tags h4 {
  font-weight: 500;
}

.sub-article__category {
  color: #5a9bd6;
  text-transform: uppercase;
  /* font-weight: 400; */
  float: left;
  display: flex;
}

.sub-article__date {
  color: #707070;
  float: right;
}
