@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  font-family: "IBM Plex Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header-container {
  padding-top: 2vh;
  /* border: 1px solid black; */
}

/* .app {
  background: #fffafa;
} */

.header {
  position: relative;
  width: 100vw;
  height: 34vw;
  /* border: 1px solid black; */
}

.header__logo {
  position: absolute;
  display: flex;
  height: 72%;
  left: 0;
  right: 0;
  margin: 0 auto;
  /* border: 1px solid black; */
}

.header__logo img {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 100%;
  /* border: 1px solid black; */
}

.header__slogan {
  position: absolute;
  height: 16%;
  width: 100%;
  margin-top: 24.8vw;
  display: flex;
  justify-content: center;
  /* border: 1px solid black; */
}

.header__slogan h4 {
  font-style: italic;
  font-size: 4vw;
  color: #707070;
}

.header__current-date {
  position: absolute;
  display: flex;
  right: 0;
  bottom: 0;
  margin-right: 6vw;
}

.header__current-date h4 {
  font-size: 3.2vw;
  font-weight: 400;
  color: #707070;
}

.featured-articles-container {
  padding: 2vh 0 1vh 0;
  display: flex;
  overflow-x: scroll;
  -ms-scroll-snap-type: x mandatory;
      scroll-snap-type: x mandatory;
  /* border: 1px solid black; */
}

.featured-article {
  margin-left: 4vw;
  scroll-snap-align: center;
  /* border: 1px solid black; */
}

.sub-article {
  margin: 0 auto;
}

.featured-article {
  position: relative;
  /* border: 1px solid black; */
}

.featured-article__image {
  min-width: 58vw;
  height: 62vw;
  border-radius: 12%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.56);
  /* border: 1px solid black; */
}

.featured-article__info {
  width: 100%;
  padding: 2% 8%;
  position: absolute;
  margin: 8% auto;
  bottom: 0;
  right: 0;
  left: 0;
  color: white;
  /* border: 1px solid black; */
}

.featured-article__title {
  font-size: 4vw;
  font-weight: 700;
}

.featured-article__tags {
  width: 96%;
  display: flex;
  margin: 1.2vh 0 0 0;
  color: white;
  font-size: 2.8vw;
  /* border: 1px solid black; */
}

.featured-article__tags h4 {
  font-weight: 500;
}

.featured-article__category {
  display: flex;
  text-transform: uppercase;
  margin-right: 0.8vw;
}

.featured-article__date {
  margin-left: 1vw;
}

.material-icons {
  color: #5a9bd6;
  margin-right: 1vw;
  font-size: 4vw;
}

