.featured-article {
  position: relative;
  /* border: 1px solid black; */
}

.featured-article__image {
  min-width: 58vw;
  height: 62vw;
  border-radius: 12%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.56);
  /* border: 1px solid black; */
}

.featured-article__info {
  width: 100%;
  padding: 2% 8%;
  position: absolute;
  margin: 8% auto;
  bottom: 0;
  right: 0;
  left: 0;
  color: white;
  /* border: 1px solid black; */
}

.featured-article__title {
  font-size: 4vw;
  font-weight: 700;
}

.featured-article__tags {
  width: 96%;
  display: flex;
  margin: 1.2vh 0 0 0;
  color: white;
  font-size: 2.8vw;
  /* border: 1px solid black; */
}

.featured-article__tags h4 {
  font-weight: 500;
}

.featured-article__category {
  display: flex;
  text-transform: uppercase;
  margin-right: 0.8vw;
}

.featured-article__date {
  margin-left: 1vw;
}

.material-icons {
  color: #5a9bd6;
  margin-right: 1vw;
  font-size: 4vw;
}

